// SLIDESHOW
//---------------------------------

.hero-slideshow {
  width: 100%;
  height: 90vh;
  // margin-top: 10vh;
  overflow: hidden;

  @media (min-width: #{$screen-lg}) {
    height: 90vh;
  }

  .slideshow-wrap {
    position: relative;
    height: 100%;
  }

  .slide {
    width: 100%;
    height: 90vh;
    position: relative;
    z-index: 0;
    background-color: $light-white;
  }

  .slide-image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: $black;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      vertical-align: bottom;
      opacity: 0.6;
    }
  }

  .slide-text {
    width: 80%;
    margin: 0 auto;

    @media (min-width: #{$screen-lg}) {
      width: 100%;
      margin: 0;
    }
  }

  .left-aligned {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 100%;

    // @media (min-width: #{$screen-lg}) {
    //   align-items: flex-start;
    // }

    * {
      text-shadow: $drop-shadow;
      margin: 0;
      text-align: left;
      width: 100%;

      // @media (min-width: #{$screen-lg}) {
      //   text-align: left;
      // }
    }
  }

  .right-aligned {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media (min-width: #{$screen-lg}) {
      align-items: flex-end;
    }

    * {
      text-shadow: $drop-shadow;
      margin: 0;
      text-align: center;

      @media (min-width: #{$screen-lg}) {
        text-align: right;
      }
    }
  }

  .middle-aligned {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100% !important;

    @media (min-width: #{$screen-md}) {
      height: fit-content !important;
    }

    * {
      text-shadow: $drop-shadow;
      margin: 0;
      text-align: center;
    }
  }


  .slide-content {
    width: 100%;
    height: fit-content;
    position: absolute;
    bottom: 0;
    padding: 60px 40px;
    left: 0;
    right: 0;
    background: rgb(0,0,0);
    background: linear-gradient(0deg, $black 0%, #24272c00 100%);


    @media (min-width: #{$screen-md}) {
      padding: 40px 10vw;
      background: transparent;

    }


    @media (min-width: #{$screen-lg}) {
      padding: 60px 10vw;


    }
    
    // @media (min-width: #{$screen-xl}) {
    //   width: 70%;
    // }

    p {
      margin-top: $line-global;
    }

    .hero-product-logo {
      width: 120px;
      margin-bottom: 20px;

      @media (min-width: #{$screen-md}) {
        width: 150px;
      }

      @media (min-width: #{$screen-xl}) {
        width: 180px;
      }
    }

    .black-friday {
      width: 70vw;
      margin: 0 auto;

      @media (min-width: #{$screen-md}) {
        width: 50vw;
      }
    }

    .helmet-product-logo {
      width: 180px;
      margin-bottom: 20px;

      @media (min-width: #{$screen-md}) {
        width: 220px;
      }

      @media (min-width: #{$screen-lg}) {
        width: 300px;
      }
    }

    .button {
      margin-top: $offset-global;
      width: fit-content;
    }
  }
}

.image-popup {
  width: 80vw;
  height: auto;

  @media (min-width: #{$screen-lg}) {
    width: 50vw;
    height: auto;
  }
}

.pop-up-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.hero-large-screen {
  display: none;

  @media (min-width: #{$screen-md}) {
    display: block;
  }
}

.hero-portrait-screen {
  display: block;

  @media (min-width: #{$screen-md}) {
    display: none;
  }
}

.rider_credit {
  position: absolute;
  bottom: 0;
  left: 0;
  right: auto;
  width: 100%;
  padding: 0 35px;

  @media (min-width: #{$screen-md}) {
    width: fit-content;
    left: auto;
    right: 0;
    padding: 0;
  }

}

.rider__credit-inner {
  padding: 5px;
  @include flexstart-flexstart;

  @media (min-width: #{$screen-md}) {
    @include space-center;
  }

  * {
    margin: 0;
    padding: 0 5px;
    color: $white;
  }
}

.riders-image {
  width: 12px;
  height: 12px;
  padding: 0;
  margin-right: 5px;
  display: flex;

  @media (min-width: #{$screen-md}) {
    width: 20px;
    height: 20px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    padding: 0;
  }
}

.riders__extra {
  @include space-center;
}

// PAGE CATEGROY
//---------------------------------

.category-image {
  height: 25vh;
  background: $black;

  @media (min-width: #{$screen-lg}) {
    height: auto;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    vertical-align: bottom;
    opacity: 0.8;

    @media (min-width: #{$screen-xxl}) {
      opacity: 1;
    }
  }
}

.homepage-category {
  width: 90%;
  margin: 0 auto;
  padding-top: 5vh;
  padding-bottom: 5vh;

  @media (min-width: #{$screen-xxxl}) {
    padding-top: 10vh;
    padding-bottom: 10vh;
    width: 90%;
  }

  .category-item-group {
    @include space-center-column;

    @media (min-width: #{$screen-lg}) {
      @include space-center;
      flex-wrap: wrap;
    }

    .category-link {
      flex: 1;
      width: 100%;
      min-width: auto;

      @media (min-width: #{$screen-lg}) {
        width: auto;
        min-width: 30%;
      }

      @media (min-width: #{$screen-xxl}) {
        min-width: auto;
      }

      .category-item {
        position: relative;
        margin: 1em 0;
        overflow: hidden;

        @media (min-width: #{$screen-lg}) {
          margin: 0.5em;
        }

        @media (min-width: #{$screen-lg}) {
          margin: 0.5em;
        }

        @media (min-width: #{$screen-xxl}) {
          margin: 1em;
        }

        .category-content {
          position: absolute;
          top: 0;
          left: 0;
          padding: 1em;
          width: 100%;
          z-index: 1;

          p {
            opacity: 0;
            color: $black;
            transition-delay: 0.3s;
            transition: all 0.3s ease-in-out;
          }

          * {
            margin-bottom: 1rem;
            transition: all 0.3s ease-in-out;
          }
        }

        .category-bottom {
          position: absolute;
          bottom: 0;
          left: 0;
          z-index: 1;
          padding: 1em;
        }
      }
    }
  }
}

.item-overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: $yellow;
  opacity: 0;
  z-index: 0;
  transition: all 0.3s ease-in-out;
  transform: translateX(-101%);
}

.category-link:hover .item-overlay {
  transform: translateX(0%);
  opacity: 0.9;
}

.category-link:hover .category-content p {
  opacity: 1 !important;
  transform: translateY(-10%);
}

.category-link:hover .category-content h5 {
  color: $black;
}

.category-link:hover .item-button {
  border-bottom-color: $black;
  color: $black;
}

// VIDEO
//----------------------------------

.video-library-grid {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;

  @media (min-width: #{$screen-md}) {
    flex-direction: row;
  }

  .video-library-section {
    width: 100%;

    @media (min-width: #{$screen-md}) {
      width: 50%;
    }

    @media (min-width: #{$screen-lg}) {
      width: 30%;
    }
  }
}

.video-section {
  position: relative;
  background: $black;
  height: 50vh;
  width: 100vw;

  @media (min-width: #{$screen-md}) {
    height: 80vh;
  }

  img {
    opacity: 0.4;
    width: 100%;
    height: 100%;
    object-fit: cover;
    vertical-align: bottom;
  }

  .video-section-title {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    top: 20%;
    width: 90%;

    transition: all 0.4s ease-in-out;
    opacity: 1;
    visibility: visible;

    @media (min-width: #{$screen-lg}) {
      top: 40%;
      width: 50%;
    }

    * {
      text-align: center;
    }
  }
}

.video-icon {
  span.inline-icon.play-button-svg {
    width: 60px;
    margin: 1em;
  }
}

.block-button {
  * {
    fill: $black;
    transition: all 0.3s ease-in-out;
    margin-bottom: 0 !important;
  }

  &:hover {
    * {
      fill: $white;
    }
  }

  .play-button-svg {
    fill: $black;
    width: 22px;
    margin-left: 5px;
  }
}

.video-item {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;

  .black-overlay {
    position: absolute;
    background: $black;
    opacity: 0.7;
    width: 100%;
    height: 100%;
    z-index: 2;
    pointer-events: all;
    transition: all 0.4s ease-in-out;
    visibility: visible;
  }

  iframe {
    width: 100vw;
    height: 56.25vw;
    /* Given a 16:9 aspect ratio, 9/16*100 = 56.25 */
    min-height: 100vh;
    min-width: 177.77vh;
    /* Given a 16:9 aspect ratio, 16/9*100 = 177.77 */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: auto;
  }
}

.hide-video-text {
  opacity: 0 !important;
  visibility: hidden !important;
}

//Page Video
.page-video-section {
  background: $black;
  position: relative;
  height: 60vh;
}

.page-video-image {
  opacity: 0.5;
  height: 100%;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    vertical-align: middle;
  }
}

.video-link {
  width: 100%;
  height: 100%;
  pointer-events: all;
}

.page-video-section-title {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  padding-top: 15vh;
  transition: all 0.4s ease-in-out;
  opacity: 1;
  visibility: visible;

  @media (min-width: #{$screen-lg}) {
    padding-top: 20vh;
  }

  * {
    text-align: center;
    fill: black;
  }

  .video-title-desc {
    max-width: 80%;
    margin: 0 auto;
  }
}

.page-video-icon {
  position: static;

  .video-pop-up-group {
    position: static;

    .block-button {
      background: transparent;
      font-size: 0;
      border: 0;
      fill: $white;
      @include space-center;

      * {
        fill: $white;
        transition: fill 0.3s ease-in-out;
      }

      .play-button-svg {
        fill: $white;
        width: 70px;
        margin: 0;
        transform: translateX(-15%);
      }

      &:hover {
        background: transparent;

        * {
          fill: $yellow;
        }
      }
    }
  }
}

// CONTENT
//----------------------------------

.content-block-collection {
  padding-top: 5vh;
  padding-bottom: 5vh;
  max-width: 90%;
  margin: 0 auto;

  @media (min-width: #{$screen-lg}) {
    max-width: 80%;
  }

  @media (min-width: #{$screen-xxl}) {
    padding-top: 15vh;
    padding-bottom: 15vh;
  }
}

.content-block-image_text {
  .image-text {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    >* {
      flex: 0 0 50%;
    }

    &.direction-rtl {
      flex-direction: row-reverse;
    }

    &.bgcolor-black {
      .block-content {
        background-color: $black;
      }
    }
  }

  .block-image {
    img {
      display: block;
      width: 100%;
    }
  }

  .block-content {
    padding: $offset-global;
  }

  .block-content-inner {
    height: 100%;
    width: 100%;
    align-items: flex-start;
    max-width: 250px;
    margin: 0 auto;
  }

  p,
