.underline-svg {
  width: 10%;
  margin-bottom: 5vh;
}

// WHAT CYCLIST ARE YOU
//--------------------------------------------

.cycle-title {
  padding-top: 0vh;
  padding-bottom: 0;
}

.reel {
  //transform: rotate(5deg);
  margin-bottom: 5vh;
}

.cycle_list {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 25vh;
  display: flex;
  align-items: center;

  @media (min-width: #{$screen-lg}) {
    height: 35vh;
  }
}

.cyclist-page-section {
  background: $black;
  @include space-center;
  height: 80vh;

  @media (min-width: #{$screen-lg}) {
    height: 60vh;
  }

  .title-section {
    .stroke {
      -webkit-text-stroke: 2px;
      -webkit-text-fill-color: transparent;
      margin-bottom: 0;
    }

    h1 {
      font-size: 4rem;
      margin-top: 0;

      @media (min-width: #{$screen-md}) {
        font-size: 5rem;
      }

      @media (min-width: #{$screen-lg}) {
        font-size: 8rem;
      }

      @media (min-width: #{$screen-xl}) {
        font-size: 10rem;
      }
    }

    h2 {
      margin-top: 0;
    }

    h2 .stroke {
      -webkit-text-stroke: 1px;
    }

    p {
      color: white;
    }
  }
}

.cycle_parent_link_yellow {
  display: block;
  margin: 0;
  position: absolute;
  width: 200%;
  animation: marquee 10s linear infinite;

  span {
    float: left;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }

  &:hover {
    animation-play-state: paused;
  }
}

.cycle_text_link_yellow {
  font-size: 3vh;
  font-weight: 700;
  text-transform: uppercase;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: $yellow;
  transition: all 0.3s ease-in-out;
  margin: 0 10px;

  &:hover {
    -webkit-text-fill-color: $yellow;
  }

  @media (min-width: #{$screen-xxxl}) {
    font-size: 5vh;
  }
}

.cycle_parent_link {
  display: flex;
  justify-content: space-between;
  margin: 0;
  position: absolute;
  width: 200%;
  animation: marquee 20s linear infinite;

  span {
    float: left;
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

    @media (min-width: #{$screen-lg}) {
      width: 20%;
    }

    @media (min-width: #{$screen-xl}) {
      width: 15%;
    }
  }

  &:hover {
    animation-play-state: paused;
  }

  .cycle_text_link {
    font-size: 8vh;
    font-weight: 700;
    text-transform: uppercase;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: $black;
    transition: all 0.5s ease-out;
    -webkit-transition: all 0.5s ease-out;
    margin: 0 10px;

    @media (min-width: #{$screen-md}) {
      font-size: 10vh;
    }

    @media (min-width: #{$screen-lg}) {
      font-size: 12vh;
    }

    @media (min-width: #{$screen-xxxl}) {
      font-size: 20vh;
    }

    &:hover {
      -webkit-text-fill-color: $black;
      transition: all 0.5s ease-out;
    }
  }
}

@keyframes marquee {
  0% {
    left: 0;
  }

  100% {
    left: -100%;
  }
}

