      @include space-center;
      flex-wrap: wrap;
    }

    .faq-menu-item {
      width: 90%;
      margin: 0 auto;
      text-align: center;

      @media (min-width: #{$screen-lg}) {
       width: fit-content;
       margin: 0;
      }

    }


    .faq-menu-item-section {
      background: $white;
      transition: all 0.6s ease;
      padding: 15px 30px;
      margin: 10px 0;

      @media (min-width: #{$screen-lg}) {
        margin: 0 10px;
      }

      p {
        margin: 0;
        color: $black;
        transition: all 0.6s ease;
      }

      &:hover {
        background: $black;
       

        p {
          color: $white;
        }
      }
    }
  }

}